
.world-section {
  border-bottom: 3px solid var(--c-granite);
  padding: 10px;
  background-color: var(--c-mint-cream);
}

.meta-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6px;
  font-weight: bold;
  font-size: 0.9rem;
  border-bottom: 1.5px dashed var(--c-grey-olive);
  padding-bottom: 6px;
  flex-wrap: wrap;
  gap: 6px;
}

.event-list {
  display: flex;
  gap: 8px;
  overflow-x: auto;
}

.event-badge {
  background: #fff;
  border: 1.5px solid var(--c-granite);
  padding: 6px 8px;
  min-width: 130px;
  flex: 1;
  position: relative;
}

.event-badge::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background-color: var(--c-celadon);
}

.event-title {
  display: block;
  font-weight: bold;
  margin-bottom: 2px;
  padding-left: 6px;
}

.event-desc {
  display: block;
  font-size: 0.75rem;
  color: var(--c-grey-olive);
  padding-left: 6px;
}

@media (max-width: 600px) {
  .meta-row {
    flex-direction: column;
    gap: 5px;
  }

  .event-list {
    flex-direction: column;
  }

  .event-badge {
    min-width: auto;
  }
}
